/**
 * 
 *
 * @class User
 */
export default class User {
  /**
   * 
   *
   * @type {string}
   * @memberof User
   */
  get token () { return this._token }
  set token (val) { this._token = val }

  /**
   * 
   * @param {string} token
   * @memberof User
   */
  constructor (token) {
    this._token = token
  }
}



