import {SceneObject} from './index' // eslint-disable-line no-unused-vars

/**
 * 
 *
 * @export
 * @class OutLine
 * @extends {Map<string, SceneObject>}
 */
export default class Outline extends Map {
  /**
   * 
   *
   * @type {boolean}
   * @readonly
   * @memberof OutLine
   */
  get allIsLoaded () {
    for (let obj of this.values()) {
      if (!obj.assetsIsLoaded) return false
    }
    return true
  }
}



