// eslint-disable-next-line no-unused-vars
import {Game, SceneObject, Outline} from './index'

/**
 * 
 *
 * @class Scene
 */
class Scene {
  /**
   * 
   *
   * @type {Game}
   * @memberof Scene
   */
  get game () { return this._game }
  set game (value) { this._game = value }
  /**
   * 
   *
   * @type {string}
   * @memberof Scene
   */
  get name () { return this._name }
  set name (value) { this._name = value }
  /**
   * 
   *
   * @type {Outline}
   * @memberof Scene
   */
  get outline () { return this._outline }
  set outline (val) { return this._outline }

  /**
   * 
   * @param {Game} game 
   * @param {string} name 
   * @memberof Scene
   */
  constructor (game, name) {
    this._game = game
    this._name = name
    this._outline = new Outline()
    this.init()
  }

  /**
   * 
   *
   * @return {Scene} 
   * @memberof Scene
   */
  init () {
    this.initOutLine()
    this.initAfter()
    return this
  }

  /**
   * 
   *
   * @memberof Scene
   */
  initOutLine () {
  }

  /**
   * 
   *
   * @memberof Scene
   */
  initAfter () {
  }

  /**
   * 
   *
   * @memberof Scene
   */
  run () {}

  /**
   * 
   * @memberof Scene
   */
  async loadAssetsAsync (onProgress) {
    const [{outline}] = [this]
    let percents = new Map() // 

    for (let [name, obj] of outline) {
      await obj.loadAssetsAsync(false, ({lengthComputable: c, loaded: l, total: t, percent: p}) => {
        percents.set(name, p)
        let percent = 0
        for (let _p of percents.values()) percent += _p
        onProgress((percent / outline.size).toFixed(2))
      }).then(root => {})
    }
    return this
  }
}

export default Scene



