/**
 * 
 *
 * @export
 * @class SceneObjectAssets
 */
export default class SceneObjectAssets {
  /**
   * 
   * @type {String}
   * @memberof SceneObjectAssets
   */
  get path () { return this._path }
  set path (val) { this._path = val }

  /**
   * 
   * @type {String}
   * @memberof SceneObjectAssets
   */
  get file () { return this._file }
  set file (val) { this._file = val }

  /**
   * 
   * @type {String}
   * @memberof SceneObjectAssets
   */
  get root () { return this._root }
  set root (val) { this._root = val }

  /**
   * 
   * @type {String[]}
   * @memberof SceneObjectAssets
   */
  get childs () { return this._childs }
  set childs (val) { this._childs = val }

  /**
   * SceneObjectAssets 
   * @param {String} path 
   * @param {String} file 
   * @param {String} root 
   * @param {String[]} childs 
   * @memberof SceneObjectAssets
   */
  constructor (path, file, root = '__root__', childs = []) {
    this._path = path
    this._file = file
    this._root = root
    this._childs = childs
  }
}



